:root {
  --very-dark-color: #222222;
  --dark-color: #505050;
  --accent-color: #757575;
}

.header {
    margin-bottom: 5%;
    font-size: 1.5em;
    color: #222222;
}

.bars{
    position: absolute;
    left: 0;
    margin-top: 45px;
    margin-left: -5px;
    display: block;
}

.avatar {
    height: 140px;
    margin-bottom: 5%;
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.text-center {
    text-align: center
}

.text-right {
    text-align: right
}

.desc {
    text-align: justify;
}

h1, h2, h3, h4, h5, h6{
    margin-bottom: 0;
    font-weight: bold;
}

a{
    text-decoration: none;
    color: #757575;
}

a:hover {
    color: #222222; 
    cursor: pointer;
}

li{
    margin-bottom: 5px;
    margin-left: 20px;
}

.mainWidget {
    margin-bottom: 7%;
}

hr{
    margin-bottom: 10px;
}

.cpy {
    text-align: right
}

.contact {
    text-align: left
}

.six.columns {
    width: 48%;
}

.columns:first-child {
    margin-left: 0;
}

.contact_icon{
    margin-left: 4px;
    margin-right: 4px;
}

.content_child_title{
    font-size: 1.1em;
}

.badge{
    font-size: 0.7em;
    margin-left: 5px;
}

.works{
    text-align: center;
}

.works_icon{
    font-size: 4em;
    color: #505050;
    margin-left: 10px;
    margin-right: 10px;
}

.wait_screen{
    margin-top: 40%;
    text-align: center;
    font-size: 2em;
}

.table-schedule{
    margin-top: 10%;
}

.table-width{
    padding: 10px 20px 10px 5px;
}

.table-head-width{
    padding-left: 5px !important;
}

.time-table-width {
    padding: 10px 20px 10px 10px;
    width: 20%;
    padding-left: 4px !important;
}

.table-schedule td{
    width: 100px;
}

.warning_screen {
    position: absolute;
    top: 40%;
    text-align: center;
    width: 100%;
    font-size: 2.5em;
}

@media (min-width: 550px) {
    .content_child_title {
        font-size: 1.3em;
    }
    .works_icon {
        font-size: 6em;
        color: #505050;
        margin-left: 10px;
        margin-right: 10px;
    }
    .time-table-width {
        padding: 10px 20px 10px 0px;
        padding-left: 4px !important;
        width: auto;
    }
}

.course-now{
    background-color: rgba(20, 20, 20, 0.7);
    color: #f6f6f6;
}

.footer{
    margin-top: 15%
}


/* code below for desktop-resz */
.sidebar {
  height: 100%;
  width: 220px;
  left: -250px;
  position: fixed;
  z-index: 1;
  top: 0;
  padding-top: 1%;
  background-color: var(--dark-color);
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  padding-left: 1%;
  padding-right: 1%;
}

.sidebar-open{
  left: 0px;
}

.sidebar-close {
  left: -250px;
}

.sidebar-button {
  color: #f6f6f6;
  font-size: 1.2em;
  display: list-item;
  padding: 2% 7%;
  -o-text-overflow: clip;
     text-overflow: clip;
  overflow: hidden;
  white-space: nowrap;
  margin: 20px 0;
}

.sidebar .active {
  background-color: var(--accent-color);
  color: var(--dark-color);
}

.main{
  -webkit-transition: margin-left .5s;
  -o-transition: margin-left .5s;
  transition: margin-left .5s;
  padding-top: var(--space-top);
  padding-right: 10%;
  padding-left: 10%;
  margin-left: 0px;
}

.main-open{
  margin-left: 250px;
}

.main-close {
  margin-left: 0px;
}

.menu-button {
    font-size: 2em;
    color: var(--very-dark-color);
    display: block;
    cursor: pointer;
    margin-left: -10px;
    margin-top: 3%;
    float: left;
}

.close-menu-button{
    font-size: 1.2em;
    color: var(--very-dark-color);
    display: block;
    cursor: pointer;
    float: right;
}

/* code below for phone-resz*/
@media screen and (max-width: 450px) {
  .sidebar {
    height: 0%;
    width: 100%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: var(--dark-color);
    overflow: hidden;
    padding-top: 0px;
    -webkit-transition: height .5s;
    -o-transition: height .5s;
    transition: height .5s;
    text-align: center;
    font-size: 1.5em;
    list-style-type: none;
  }

  .sidebar-open {
    height: 100%;
  }

  .sidebar-close {
    height: 0%;
  }

  .sidebar .active {
    background-color: transparent;
    color: var(--accent-color);
  }

  .sidebar-button{
    color: var(--very-dark-color)
  }

  .main{
    margin-left: 0px;
    -webkit-transition: margin-top 0.5s;
    -o-transition: margin-top 0.5s;
    transition: margin-top 0.5s;
    padding: 0 6%;
  }

  .main-open{
    margin-top: 100%;
  }

  .main-close{
    margin-top: 0%
  }

  .menu-button {
    font-size: 2em;
    color: var(--very-dark-color);
    display: block;
    cursor: pointer;
    margin-right: -10px;
    margin-top: 11%;
    float: right;
  }

  .close-menu-button {
      font-size: 1.2em;
      color: var(--very-dark-color);
      display: block;
      cursor: pointer;
      float: right;
      margin-top: 20px;
      margin-right: 20px;
  }
}



